//引入依赖及软件
import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

import pagesOne from '../components/pagesOne.vue'
import pagesTwo from '../components/pagesTwo.vue'

export default new Router({
    routes:[
        {
            pathL:'/',
            name:'pagesOne',
            component: pagesOne
        },
        {
            pathL:'/pagesOne',
            name:'pagesOne',
            component: pagesOne
        },
        {
            pathL:'/pagesOTwo',
            name:'pagesTwo',
            component: pagesTwo
        },
]
})